<template>
  <div class="me-loading-wrap">
    <transition name="slide-fade">
      <div class="me-loading" v-show="visible">
        <div class="me-loading__hd">
          <span class="loading-icon"></span>
        </div>
        <div class="me-loading__bd">
          {{msg}}
        </div>
      </div>
    </transition>
    <div class="me-mask"></div>
  </div>

</template>

<script>
export default {
  props: {
    msg: String,
  },
  data () {
    return {
      visible: false
    };
  }
}
</script>

<style lang='stylus' scoped>
  .me-loading-wrap
    position: fixed
    top: 0
    left: 0
    width 100%
    height 100%
    z-index 1001
    display flex
    align-items center
    justify-content center
    .me-loading
      margin-top -20%
      background: rgba(17, 17, 17, 0.7)
      text-align: center
      border-radius: 5px
      width auto
      color: #FFFFFF
      padding 10px 0px
      z-index 1000
      .me-loading__hd
        padding 8px 0
      .me-loading__bd
        text-align center
        width 100px
        padding 6px 10px
        line-height 1.4

.slide-fade-enter-active, .slide-fade-leave-active
    transition: all 0.3s ease

.slide-fade-enter, .slide-fade-leave-to
  transform: scale(1.1)
  transform: translateY(-10px)
  opacity: 0

.loading-icon
  width: 50px;
  height: 50px;
  display: inline-block;
  vertical-align: middle;
  animation: weuiLoading 1s steps(12, end) infinite;
  background: transparent url("data:image/svg+xml;base64,") no-repeat;
  background-size: 100%;

@keyframes weuiLoading {
  0% {
    transform: rotate3d(0, 0, 1, 0deg);
  }

  100% {
    transform: rotate3d(0, 0, 1, 360deg);
  }
}
 </style>



